@import url(https://fonts.googleapis.com/css?family=Quicksand&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

.row-height {
	height: 30vh;
}

.row-m-p {
	margin: 0 0 10px 0;
	padding: 5px;
}
.col-m-p {
	margin: 5px;
	padding: 5px;
}

.dark-color-bg {
	background-color: rgb(38, 38, 38);
}
.gray-color-bg {
	background-color: rgb(102, 102, 102);
}
.blue-color-bg {
	background-color: rgb(212, 231, 247);
}
.beige-color-bg {
	background-color: rgb(252, 230, 207);
}
.gold-color-bg {
	background-color: rgb(233, 181, 99);
}

.image-size {
	max-width: 100%;
	max-height: calc(30vh - 20px);
}

.container,
.home-row {
	min-height: 100vh;
}

#side-navigation {
	position: absolute;
	right: 0;
	top: 0;
	min-height: 90vh;
	width: 20vw;
}

#side-navigation a {
	position: absolute;
	right: -50px;
	transition: 0.5s;
	padding: 10px;
	margin: 2px 0;
	width: 100px;
	text-decoration: none;
	font-size: 20px;
	color: white;
	border-radius: 5px 0 0 5px;
}

#side-navigation a:hover {
	right: 0;
}

#home {
	top: 10px;
	background-color: rgb(38, 38, 38);
}
#search {
	top: 70px;
	background-color: rgb(38, 38, 38);
}

#about {
	top: 130px;
	background-color: rgb(38, 38, 38);
}

#gallery {
	top: 190px;
	background-color: rgb(38, 38, 38);
}

#contacts {
	top: 250px;
	background-color: rgb(38, 38, 38);
}

#side-navigation .active {
	right: 0;
}

html,
#root {
	min-height: 100vh;
	width: 100vw;
	font-family: 'Quicksand', 'Arial', sans-serif;
	margin: 0;
	padding: 0;
}

/*colors
- dark -> rgb(38, 38, 38)
- gray -> rgb(102, 102, 102)
- light blue -> rgb(212, 231, 247)
- beige -> rgb(252, 230, 207)
- gold -> rgb(233, 181, 99)
*/

